import React from "react"
import Dialog from '@material-ui/core/Dialog';
import { FaTimes } from "react-icons/fa"
import { LayoutContainer, ErrorText, CloseIcon } from "./DialogElement"

export default function ErrorDialog({ open, handleClose }) {
    return (
        <div>
            <Dialog
                open={open}
                onClose={(event, reason) => {
                    if (reason !== 'backdropClick') {
                        handleClose();
                    }
                }}
            >
                <LayoutContainer>
                    <CloseIcon onClick={handleClose}>
                        <FaTimes />
                    </CloseIcon>

                    <ErrorText style={{ marginTop: "20px" }}>You are currently connected to the unsupported network. Please switch to the Etheremum MainNet</ErrorText>
                </LayoutContainer>
            </Dialog>
        </div >
    );
}