import React, { useState } from "react"
import { CardOuterBox, CardInnerBox, InnerImg, CountdownBox, TextInfo, BlockchainButton, ErrorTextInfo, CustomSpan, WarningTextInfo } from "./MintCardElements"
import Countdown from 'react-countdown';
import Sale from "./sales";
import { useWallet } from 'use-wallet'
import { useNftContract } from "../../hooks/useContract";
import TransactionDialog from "../Dialog/TranscationDialog";

const MintCard = ({ infoWithAccount, baseInfo }) => {
    const [startDate] = useState(1631390400000)
    const [transactionHash, setTransactionHash] = useState("")
    const [loading, setLoading] = useState(false)
    const [open, setOpen] = React.useState(false);
    const [error, setError] = useState("")
    const hasRole = infoWithAccount.hasRole
    const airdropSuccess = infoWithAccount.airdropSuccess
    const maxPenguins = baseInfo.maxPenguins || 10000
    const currentSupply = baseInfo.currentSupply
    const supplyPercentage = currentSupply / maxPenguins

    const { account } = useWallet()
    const isAddressNull = account === null

    const nftContract = useNftContract()

    const claimNft = async () => {
        setError("")
        setLoading(true)
        setTransactionHash("")
        handleClickOpen()
        try {
            const result = await nftContract.methods.claim().send({ from: account })

            if (result) {
                setTransactionHash(result.transactionHash)
                setLoading(false)
            }
        } catch (err) {
            setError("Oops! Failed to claim the penguin! Please try again")
            setLoading(false)
        }
    }

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClickClose = () => {
        setOpen(false);
    };

    let comp;
    let supplyPercentageComp;

    if (supplyPercentage === 1) {
        supplyPercentageComp = <WarningTextInfo>All penguins has been sold!</WarningTextInfo>
    } else if (supplyPercentage >= 0.75) {
        supplyPercentageComp = <WarningTextInfo>75% penguins has been sold!</WarningTextInfo>
    } else if (supplyPercentage >= 0.5) (
        supplyPercentageComp = <WarningTextInfo>50% penguins has been sold!</WarningTextInfo>
    )

    if (!isAddressNull) {
        if (hasRole) {
            if (airdropSuccess) {
                comp = <ErrorTextInfo>You have already claimed the penguin!</ErrorTextInfo>
            } else {
                comp = <TextInfo>Congrats, you have 1 free penguin claim!</TextInfo>
            }
        }
    }

    return (
        <div style={{ display: "flex", flexDirection: "column", alignItems: "center" }}>
            <CardOuterBox>
                <CardInnerBox>
                    <InnerImg src="./images/motion.gif" />
                </CardInnerBox>
            </CardOuterBox>

            <Countdown date={startDate} renderer={renderer} />

            <BlockchainButton disabled={isAddressNull || !hasRole || airdropSuccess} onClick={claimNft}>
                Free Claim
            </BlockchainButton>

            <TransactionDialog open={open} handleClose={handleClickClose} transactionHash={transactionHash} loading={loading} error={error} />
            {comp}
            {supplyPercentageComp}
        </div >
    )
}

const renderer = ({ total, days, hours, minutes, seconds }) => {
    if (total) {
        return (
            <div style={{ display: "flex", flexDirection: "column", alignItems: "center" }}>
                <CountdownBox>
                    <CustomSpan>{days}d:{hours}h:{minutes}m:{seconds}s</CustomSpan>
                </CountdownBox>

                <TextInfo>The penguin sale is starting soon.</TextInfo>
            </div>

        );
    } else {
        return <Sale />;
    }
};

export default MintCard